.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: rgba(240, 248, 255, 0.483);
    background-image: radial-gradient(rgb(66, 66, 66), transparent);
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.title{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.space{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  border-bottom: 1px solid #000;
}

.camp{
  display: flex;
  flex-direction: column;
}

.inp {
  display: block;
  margin-bottom: 5px;
  font-style: oblique;
}

.value {
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 16px;
}

.foto{
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}
.icon{
  width: 30px;
  height: 30px;
}
.btn{
  width: 15vw;
  height: 5vh;
  border-radius: 10px;
  margin-top: 15px;
  font-size: 20px;
  font-style: oblique;
  margin-bottom: 5px;
}
.btn:hover{
  background-color: black;
  color: white;
  border-color: white;
}